import React, { memo, useMemo, useCallback, useState, useRef } from "react"
import style from "./style.module.scss"
// ref    (  )
import Checkbox from "@mui/material/Checkbox"
//   mui
import DeleteIcon from "@mui/icons-material/Delete"
import Button from "@mui/material/Button"

type TodosProps = {
  userId: number
  id: number
  title: string
  completed: boolean
}[]

function Todos() {
  // useState , todo .
  //  []   , useEffect    todos 
  const [todos, setTodos] = useState<TodosProps>([])
  const [text, setText] = useState("")

  const nextId = useRef(1)

  const onChange = useCallback((e: { target: { value: string } }) => {
    const { value } = e.target
    // input validation
    if (value.length > 100) {
      alert(" 100  .")
      return
    }
    setText(value)
  }, [])

  //    
  const onCreate = useCallback(
    (e: { preventDefault: () => void }) => {
      e.preventDefault() // form    
      if (text.length <= 0) {
        alert(" .")
        return
      }

      //     todolist 
      const result = [
        ...todos,
        {
          userId: 1,
          id: nextId.current,
          title: text,
          completed: false,
        },
      ]
      nextId.current++
      setTodos(result)
      setText("") //     
    },
    [text, todos]
  )

  //    
  const onComplete = useCallback(
    (id: number) => {
      const result = todos.map((item) => {
        return id === item.id
          ? { ...item, completed: !item.completed }
          : { ...item }
      }) // onComplete . id ,  id todo id    completed item completed 
      setTodos(result) // result  setTodos (setStates)
    },
    [todos]
  )
  // todos     todos 

  //    
  const onDelete = useCallback(
    (id: number) => {
      const result = todos.filter((item) => {
        return id !== item.id
      })
      setTodos(result)
    },
    [todos]
  )

  //   
  const render = useMemo(
    () =>
      todos.map((item) => {
        // render   todos map  return
        return (
          // item return   ,  key  item id 
          <div key={item.id}>
            <span>#{item.id}.</span>
            <span>{item.title}</span>
            <span onClick={() => onComplete(item.id)}>
              <Checkbox defaultChecked={item.completed} />
            </span>
            <span className={style.deleteIcon}>
              <DeleteIcon onClick={() => onDelete(item.id)}></DeleteIcon>
            </span>
          </div> // completed true false   
          //   item.id  onComplete  
          // true defaultChecked
        ) // span 
      }),
    [onComplete, onDelete, todos]
  )

  return (
    <>
      <div className={style.App}>
        <h1>Todo App</h1>
        <form onSubmit={onCreate}>
          <input
            className={style.inputText}
            name="todo"
            type="text"
            value={text}
            onChange={onChange}
            placeholder="  "
          ></input>
          <Button type="submit" variant="contained">
            
          </Button>
        </form>
        <div>{render}</div>
      </div>
    </>
  )
}

export default memo(Todos)
